.card {
  max-width: 22.98%;
  display: flex;
  flex-direction: column; }
  .card__body {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem;
    border: 1px solid #DEE2E6;
    border-width: 0 1px 1px 1px;
    border-radius: 0 0 .25rem .25rem;
    box-shadow: 0px 2px 6px #00000029; }
  .card__img {
    max-height: 256px;
    border-radius: .25rem .25rem 0 0;
    object-fit: cover; }
  .card__title {
    font-size: 0.875rem;
    line-height: 21px;
    margin-bottom: 0.5rem; }
  .card__footer {
    font-size: 0.875rem;
    color: #6c757d;
    margin-top: 1rem; }

.form {
  max-width: 730px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px; }
  .form__text {
    font-size: 1.25em;
    margin-bottom: 1.25rem; }
  .form__label {
    margin-bottom: 0.25rem; }
  .form__control {
    display: block;
    width: 100%;
    border: 1px solid #000;
    background: none;
    outline-color: #AA0601;
    padding: .75rem 1rem .875rem 1rem; }
  .form__textarea {
    min-height: 136px; }
  .form__checkbox {
    display: none; }
  .form__checkbox + .form__checkboxSpan {
    display: inline-block;
    background: url(../images/check_box_outline_blank_black_24dp.svg) no-repeat left top;
    cursor: pointer;
    user-select: none;
    /* 防止文字被滑鼠選取反白 */
    padding-left: 28px;
    margin-bottom: 40px; }
  .form__checkbox:checked + .form__checkboxSpan {
    background: url(../images/check_box_black_24dp.svg) no-repeat left top; }
  .form__submit {
    width: 350px;
    height: 100%;
    line-height: 54px;
    border: none;
    background: #AA0601;
    color: #fff; }
  .form ::placeholder {
    opacity: 0.2; }

.nav {
  display: flex;
